import React from 'react';
import { DialogPage, DialogTitle, FullscreenDecoration } from 'components/DialogPage/DialogPage';
import { SplendidAnimation } from 'components/SplendidAnimation/SplendidAnimation';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { layout } from 'style/const/layout';

import waitAnimationData from './Wait.assets/wait.animation.json';

export const Wait: React.FC = () => {
    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <SplendidAnimation
                    width={layout.widthPx}
                    height={layout.heightPx}
                    delayMs={0}
                    config={{
                        animationData: waitAnimationData,
                    }}
                />
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPage decorations={decorations}>
            <DialogTitle>
                <MultilineText text={'Оформляем\n заказ'} />
            </DialogTitle>
        </DialogPage>
    );
};
