import { useKeyboardClose } from 'hooks/useKeyboardClose';
import React from 'react';
import { addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullDoorCodeInputChangeAction } from 'store/actions/addressFullPage';
import { Input } from 'components/Input/Input';

const StyledInput = styled(Input)`
    margin: 80px auto 0;
    width: 900px;
`;

export const AddressFullDoorCode = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.doorCodeInputValue);
    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressFullDoorCodeInputChangeAction(e.target.value));
        },
        [dispatch],
    );
    /**
     * Логично предположить что этот коллбек отработает два раза.
     * Первый при нажатии "ок" в клавиатуре, второй при закрытии клавиатуры.
     * Но так как закрытие клавиатуры хендлится через эффект, который в
     * свою очередь подчиняется lifecycle'у компонента, обработчик не сработает,
     * потому что к этому времени компонент уже размонтирован.
     */
    const handleKeyboardClose = React.useCallback(() => {
        dispatch(addressFullValidationOkThunk());
    }, [dispatch]);

    // useKeyboardClose(handleKeyboardClose);

    return (
        <StyledInput
            title="Комментарий"
            value={inputValue}
            shouldFocusInputOnMount
            onChange={handleInputChange}
            onSubmit={handleKeyboardClose}
        />
    );
};
