import { AppReducer } from 'store/constants';
import { GoToCatalogCommandPayload } from 'store/constants/native';
import { StoreUser } from 'store/constants/user';

const normalizeUserData = (payload: GoToCatalogCommandPayload): StoreUser => {
    return {
        customerName: payload.address.customer_name,
        phoneNumber: payload.address.phone_num,
        addressLocationLatitude: payload.address.address.location.latitude,
        addressLocationLongitude: payload.address.address.location.longitude,
        deviceLocationLatitude: payload.device_location.latitude,
        deviceLocationLongitude: payload.device_location.longitude,
        address: payload.address.address.address_string,
        city: payload.address.address.city,
        street: payload.address.address.street,
        house: payload.address.address.house,
        building: payload.address.address.building,
        floor: payload.address.address.floor,
        entrance: payload.address.address.entrance,
        apartment: payload.address.address.apartment,
        doorCode: payload.address.address.door_code,
        isPhoneValid: true,
    };
};

export const userData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'GO_TO_PROMO': {
            const { latitude, longitude } = action.payload.device_location;
            state.userData.deviceLocationLatitude = latitude;
            state.userData.deviceLocationLongitude = longitude;
            break;
        }

        case 'GO_TO_CATALOG': {
            state.userData = normalizeUserData(action.payload);
            break;
        }

        case 'ADDRESS_FULL_SUCCESS': {
            state.userData.phoneNumber = state.userModel.phoneNumber;
            state.userData.apartment = state.userModel.apartment;
            state.userData.entrance = state.userModel.entrance;
            state.userData.floor = state.userModel.floor;
            state.userData.doorCode = state.userModel.doorCode;

            break;
        }

        case 'LAST_ORDER_INFO': {
            const payload = action.payload.info;

            state.userData = {
                addressLocationLatitude: payload.address.location.latitude,
                addressLocationLongitude: payload.address.location.longitude,
                customerName: payload.customer.name,
                phoneNumber: payload.customer.phone,
                city: payload.address.city,
                street: payload.address.street,
                house: payload.address.house,
                floor: payload.address.floor,
                entrance: payload.address.entrance,
                apartment: payload.address.apartment,
                doorCode: payload.address.door_code,
                address: `${payload.address.street}, ${payload.address.house}`,
                building: '',
                deviceLocationLatitude: payload.address.location.latitude,
                deviceLocationLongitude: payload.address.location.longitude,
                isPhoneValid: true,
            };

            break;
        }

        default:
            break;
    }
};
