import { SberVisorAnalytics, SberVisorPushParams, SberVisorPushProps } from '@frntnd/analytics/lib/sbervisor';
import amplitude, { AmplitudeClient } from 'amplitude-js';

const sberVisorApiKey = '6a619dbfc07f390933233c530907f92fc89607711eed7508d4d59cb8636b674a';
const sberVisorEndpint = 'https://sa.online.sberbank.ru:8098/metrics/partners';

const isProdution =
    window.location.host.startsWith('https://salute.online.sberbank.ru/static/services/foodtech') ||
    window.location.host.startsWith('https://smartapp-studio.sberdevices.ru/static/services/foodtech') ||
    window.location.host.startsWith('https://static.sberdevices.ru/static/services/foodtech');

const amplitudeApiKey = isProdution ? '7708ad44aba64d66d6f0f7aa06da4378' : '8a7afa2bca611718d7ed11baaea3d14d';

type AnalyticsPushParams = SberVisorPushParams;

interface AnalyticsOptions {
    tags?: string[];
    properties?: SberVisorPushProps;
}

interface AnalyticsEngine {
    type: string;
    setUserId(userId: string): void;
    push(params: AnalyticsPushParams): void;
}

class SberAnalytics implements AnalyticsEngine {
    public type = 'sber';

    private userId = '';

    private instance = new SberVisorAnalytics({
        url: sberVisorEndpint,
        apiKey: sberVisorApiKey,
        buffer: 1,
    });

    public setUserId(userId: string): void {
        this.userId = userId;
    }

    public push(params: AnalyticsPushParams) {
        this.instance.sendEvent(() => ({
            ...params,
            properties: { ...params.properties, userId: this.userId },
        }));
    }
}

class AmpAnalytics implements AnalyticsEngine {
    public type = 'amp';

    private instance: AmplitudeClient = amplitude.getInstance();

    public constructor() {
        this.instance.init(amplitudeApiKey);
    }

    public setUserId(userId: string): void {
        this.instance.setUserId(userId);
    }

    public push(params: AnalyticsPushParams) {
        const { eventAction: event, ...data } = params;
        this.instance.logEvent(event, data);
    }
}

function createAnalyticsEngine(): AnalyticsEngine[] {
    const amp = new AmpAnalytics();
    const sber = new SberAnalytics();
    return [amp, sber];
}

class Analytics {
    private analytics: AnalyticsEngine[];

    public constructor(analytics: AnalyticsEngine[]) {
        this.analytics = analytics;
        const userId = (Date.now() + Math.floor(Math.random() * 1000)).toString();
        this.setUserId(userId);
        console.log('userId', userId);
        this.push({
            eventAction: 'Cтарт',
            eventCategory: 'foodtech',
            eventType: 'init',
            value: 'start',
        });
    }

    public setUserId(userId: string) {
        this.analytics.forEach((a) => a.setUserId(userId));
    }

    public push(params: AnalyticsPushParams) {
        this.analytics.forEach((a) => a.push(params));
    }

    public remoteNavigate(label: string, ops?: AnalyticsOptions) {
        this.push({
            eventAction: label,
            eventCategory: 'foodtech',
            eventType: 'navigate',
            value: label,
            properties: ops?.properties,
            tags: ops?.tags,
        });
    }

    public redux(label: string, action: any, ops?: AnalyticsOptions) {
        const { payload = {} } = action;
        this.push({
            eventAction: label,
            eventCategory: 'foodtech',
            eventType: 'redux',
            value: ops?.toString() || '',
            properties: payload,
            tags: ops?.tags,
        });
    }
}

export const analyticsInstance = new Analytics(createAnalyticsEngine());
