import { useIsValueChanged } from 'hooks/useIsValueChanged';
import { useKeyboardClose } from 'hooks/useKeyboardClose';
import React from 'react';
import { ROUTES } from 'store/constants/router';
import styled from 'styled-components';
import { goBack, replace } from 'connected-react-router';
import { Input } from 'components/Input/Input';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressChangeInputValueAction } from 'store/actions/addressSimplePage';

const StyledInput = styled(Input)`
    margin: 54px auto 0;
    width: 900px;
`;

export const AddressSimpleInput = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const isInputValueChanged = useIsValueChanged(inputValue);
    /**
     * Логично предположить что этот коллбек отработает два раза.
     * Первый при нажатии "ок" в клавиатуре, второй при закрытии клавиатуры.
     * Но так как закрытие клавиатуры хендлится через эффект, который в
     * свою очередь подчиняется lifecycle'у компонента, обработчик не сработает,
     * потому что к этому времени компонент уже размонтирован.
     */
    const handleKeyboardClose = React.useCallback(() => {
        if (isInputValueChanged) {
            dispatch(replace(ROUTES.ADDRESS_SIMPLE_QUESTION));
        } else {
            dispatch(goBack());
        }
    }, [dispatch, isInputValueChanged]);

    useKeyboardClose(handleKeyboardClose);

    const handleInputChange = React.useCallback(
        (e: React.ChangeEvent<HTMLInputElement>) => {
            dispatch(addressChangeInputValueAction(e.target.value));
        },
        [dispatch],
    );

    return (
        <StyledInput
            shouldFocusInputOnMount
            title="Адрес"
            value={inputValue}
            onChange={handleInputChange}
            onSubmit={handleKeyboardClose}
        />
    );
};
