import React from 'react';
import styled from 'styled-components';
import { Spinner } from 'components/Spinner/Spinner';
import { BodyText1 } from 'components/Text/Body';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { color } from 'style/const/color';

const StyledContainer = styled.div`
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
`;

const StyledText = styled(BodyText1)`
    margin-top: 32px;

    color: ${color.text.primary};
`;

interface SpinnerPageProps {
    text?: string;
}

export const SpinnerPage: React.FC<SpinnerPageProps> = ({ text = '' }) => (
    <StyledContainer>
        <Spinner />
        {text && (
            <StyledText>
                <MultilineText text={text} />
            </StyledText>
        )}
    </StyledContainer>
);
